    margin: 20px 0 0 0;
  }
}
.we-offer__information-item:not(:first-child) {
  margin: 24px 0 0 0;
}
.we-offer__information-item p {
  margin: 12px 0 0 0;
  font-size: 16px;
  line-height: 160%;
  color: #787a80;
  padding: 0 0 0 45px;
  height: 0;
  width: 0;
  overflow-y: hidden;
  transition: width 0.2s ease;
}
@media (max-width: 480px) {
  .we-offer__information-item p {
    font-size: 12px;
  }
}
@media (max-width: 992px) {
  .we-offer__information-item:not(:first-child) {
    margin: 12px 0 0 0;
  }
}
.we-offer__information-button {
  width: 100%;
  display: inline-flex;
  align-items: center;
  column-gap: 20px;
  font-family: "Ubu-B";
  font-size: 28px;
  line-height: 150%;
  text-align: left;
}
@media (max-width: 992px) {
  .we-offer__information-button {
    font-size: 22px;
    column-gap: 10px;
  }
}
@media (max-width: 480px) {
  .we-offer__information-button {
    font-size: 16px;
  }
}
.we-offer__information-button div {
  position: relative;
  min-width: 25px;
  height: 25px;
}
.we-offer__information-button div::before, .we-offer__information-button div::after {
  content: "";
  position: absolute;
  top: 50%;
  width: 22px;
  height: 2px;
  border-radius: 1px;
  background-color: #ff5a30;
  transition: all 0.6s ease;
}
@media (max-width: 992px) {
  .we-offer__information-button div::before, .we-offer__information-button div::after {
    width: 16px;
  }
}
.we-offer__information-button div::after {
  transform: rotate(90deg);
}
.we-offer__information-button:focus ~ p {
  height: 130px;
  width: 100%;
  overflow-y: auto;
}
@media (max-width: 480px) {
  .we-offer__information-button:focus ~ p {
    height: 60px;
    margin: 0;
  }
}
.we-offer__information-button:focus div {
  transform: rotate(360deg);
}
.we-offer__information-button:focus div::after {
  transform: rotate(0);
}

