body {
  width: 100%;
  min-height: 100%;
  font-family: "Ubu-R";
  -webkit-user-select: none;
          user-select: none;
}

.invisible {
  opacity: 0;
  transform: scaleY(0);
  transition: all 1s ease;
}

.visible {
  opacity: 1;
  transform: scaleY(1);
  transition: all 1s ease;
}

.header-background {
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  background-color: rgba(0, 0, 0, 0.2);
  box-shadow: 0 5px 20px 1px rgba(0, 0, 0, 0.3);
}

.wrapper {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template: minmax(90px, auto) 1fr minmax(1fr, 825px) minmax(566px, auto)/1fr;
  grid-template-areas: "header" "main" "discuss" "footer";
}

.header {
  grid-area: header;
  z-index: 1000;
  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  transition: all 0.5s ease;
}

.main {
  grid-area: main;
  display: flex;
  flex-direction: column;
}

.discuss {
  grid-area: discuss;
}

.footer {
  grid-area: footer;
  background-color: #1e212c;
}

.parallax-block {
  width: 100%;
  height: 600px;
  position: fixed;
  top: 0;
  background-color: #f4f5f6;
  z-index: -1;
}

.full-viewport {
  height: 100vh;
  top: 90px;
}

.name-page-block {
  height: 510px;
}
@media (max-width: 768px) {
  .name-page-block {
    height: 400px;
  }
}
.name-page-block__body {
  margin: 70px 0 0 0;
  max-width: 600px;
}
@media (max-width: 768px) {
  .name-page-block__body {
    width: 100%;
  }
}
.name-page-block__list {
  color: #424551;
  font-size: 14px;
  line-height: 150%;
  display: inline-flex;
}
@media (max-width: 768px) {
  .name-page-block__list {
    background-color: white;
    padding: 5px 15px;
    border-radius: 10px;
    box-shadow: 0 0 10px 0.5px rgba(0, 0, 0, 0.5);
  }
}
.name-page-block__list li a {
  transition: all 0.5s ease;
}
.name-page-block__list li:last-child {
  color: #9a9ca5;
}
.name-page-block__title {
  margin: 40px 0 0 0;
  font-family: "Ubu-B";
  color: #1e212c;
  font-size: 72px;
  line-height: 130%;
  letter-spacing: 1px;
}
@media (max-width: 992px) {
  .name-page-block__title {
    margin: 30px 0 0 0;
    font-size: 50px;
  }
}
@media (max-width: 768px) {
  .name-page-block__title {
    font-size: 34px;
  }
}
.name-page-block__text {
  color: #787a80;
  margin: 24px 0 0 0;
  font-size: 20px;
  line-height: 150%;
}
@media (max-width: 768px) {
  .name-page-block__text {
    margin: 20px 0 0 0;
    font-size: 17px;
    background-color: rgba(255, 255, 255, 0.8);
    padding: 5px 15px;
    border-radius: 10px;
    box-shadow: 0 0 10px 0.5px rgba(0, 0, 0, 0.5);
  }
}
@media (max-width: 480px) {
  .name-page-block__text {
    padding: 20px 0 20px 5px;
    row-gap: 30px;
  }
}

.go-top {
  position: fixed;
  display: flex;
  column-gap: 16px;
  bottom: 4%;
  right: 4%;
  background: none;
  opacity: 0;
  transition: all 0.5s ease;
  font-weight: 600;
  text-shadow: 0 0 4px rgba(255, 255, 255, 0.6);
  z-index: 100;
}
.go-top__text {
  align-self: flex-end;
  font-size: 14px;
  color: #ff5a30;
  line-height: 150%;
}
@media (max-width: 992px) {
  .go-top__text {
    margin: 10px 0 0;
    align-self: flex-end;
    writing-mode: vertical-lr;
    font-size: 16px;
  }
}
.go-top__squer {
  width: 40px;
  height: 40px;
  border-radius: 4px;
  background: #ff5a30;
  position: relative;
  overflow: hidden;
}
.go-top__squer::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  top: 65%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  border-left: 3px solid #fff;
  border-top: 3px solid #fff;
}
@media (max-width: 992px) {
  .go-top__squer {
    width: 15px;
    height: 25px;
  }
  .go-top__squer::before {
    width: 10px;
    height: 10px;
  }
}
@media (max-width: 992px) {
  .go-top {
    flex-direction: column-reverse;
    right: 5px;
    align-items: center;
  }
}

.success-block {
  display: flex;
  width: 100%;
  height: 100%;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  top: 0;
  left: -1px;
  text-align: center;
  background-color: #1e212c;
  border: 1px solid #ff5a30;
  border-radius: 6px;
  box-shadow: 0 0 10px 1px rgba(255, 90, 48, 0.6);
  z-index: 100;
  transition: all 0.5s ease;
  transform: scaleY(0);
}
.success-block h5 {
  margin: 40px 0 0;
  font-size: 40px;
  -webkit-user-select: none;
          user-select: none;
  font-family: "Ubu-B";
  color: #ff5a30;
  text-shadow: 0 0 4px rgba(255, 255, 255, 0.5);
  letter-spacing: 0.2rem;
}
.success-block p {
  margin: 50px 30px 0;
  font-size: 30px;
  line-height: 130%;
}
.success-block p::before {
  content: attr(data-success-text);
  color: #fff;
}
.success-block button {
  margin: 100px 0 40px;
  align-self: center;
